@use "icon" as *;
@use "button" as *;
@use "accordion" as *;
@use "mixins" as *;

.accordion-list {
	display: flex;
	flex-direction: column;
	gap: gap-scale(1);

	&__header {
		display: flex;
		justify-content: flex-end;
	}

	&__toggler {
		cursor: pointer;
		--icon-size: 28;
		@extend %button-text;

		&::after {
			content: "";
			@extend %icon, %icon--expand;
		}
	}

	&:has([data-accordion].is-active) &__toggler::after {
		@extend %icon--collapse;
	}

	&__item {
		@include accordion;
	}
}