@use "font" as *;
@use "button" as *;
@use "breakpoint" as *;
@use "mixins" as *;

.webinar-card {
	height: 100%;
	display: flex;
	flex-direction: column;

	&__header {
		display: grid;
		min-height: 230px;
		border-radius: var(--default-border-radius) var(--default-border-radius) 0 0;
		background-color: var(--color-text);

		@media (max-width: $mobile) {
			min-height: 160px;
		}

		&__img,
		&__text {
			grid-row: 1;
			grid-column: 1;
			border-radius: inherit;
		}

		&__img {
			height: 0;
			min-height: 100%;
			object-fit: cover;
			@include themeHide;
		}

		&__text {
			display: flex;
			align-items: flex-end;
			color: var(--color-text-alt);
			background-color: color-mix(in srgb, var(--color-text) 30%, #0000);
			padding: gap-scale(1.5);
			text-shadow: 0 0 4px color-mix(in srgb, var(--color-text) 80%, #0000);
			@extend %h4;
		}
	}

	&__body {
		display: flex;
		flex-direction: column;
		gap: gap-scale(0.5);
		padding: gap-scale(1.5);
		border-width: 0 var(--default-border-width) var(--default-border-width) var(--default-border-width);
		border-style: solid;
		border-color: var(--default-border-color);
		border-radius: 0 0 var(--default-border-radius) var(--default-border-radius);
		flex-grow: 1;
	}

	&__date {
		color: var(--color-primary);
	}

	&__presenters {
		display: flex;
		flex-direction: column;
		margin-bottom: gap-scale(0.5);
	}

	&__link {
		margin-top: auto;
		align-self: flex-start;
		@extend %button;

		&--alt {
			@extend %button--accent;
		}
	}
}